<template>
  <div>
    <div class="list-title">
      <img class="title-icon" src="http://img1.qunarzz.com/piao/fusion/1711/89/ebc329f16c55bb02.png" alt="标题icon">
      周末去哪儿
    </div>
    <ul>
      <li class="list-item border-bottom" v-for="item of list" :key="item.id">
        <div class="item-img-wraper">
          <img class="item-img" :src="item.imgUrl" alt="">
        </div>
        <div class="item-content">
          <p class="item-title">{{item.title}}</p>
          <p class="item-desc">{{item.desc}}</p>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'HomeWeekend',
  props: {
    list: Array
  }
}
</script>

<style lang="stylus" scoped>
  @import '~styles/mixins.styl'
  .list-title
   line-height :.90rem
   font-size :.32rem
   color :#212121
   text-indent :.2rem   //首行缩进
   background :#eee
   .title-icon
    display :inline-block
    width :.3rem
    height :.3rem
    line-height :.90rem
  .list-item
   overflow :hidden
   .item-img-wraper
    overflow :hidden
    height :0
    padding-bottom :37.08%
    .item-img
      width : 100%
   .item-content
     display :block
     padding :.2rem
     .item-title
      line-height :.52rem
      font-size :.28rem
      ellipsis()
     .item-desc
      line-height :.48rem
      font-size :.24rem
      color :#ccc
      ellipsis()
</style>



