<template>
  <div class="nav-container border-top">
    <div class="nav-site border-right">
      <div class="nav-wrap-text">
        <span class="iconfont nav-icon">&#xe611;</span>定位失败
      </div>
    </div>
    <div class="nav-play">
      <div class="nav-wrap-text">
        <span class="iconfont nav-icon">&#xe60a;</span>玩水季
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HomeNav'
}
</script>

<style lang="stylus" scoped>
  .border-top, .border-right
    &:before
      border-color: #ccc
  .nav-container
    width :100%
    background :#fff
    height :0
    padding-bottom :12.5%
    .nav-site, .nav-play
      position :relative
      float :left
      width :50%
      height :0
      padding-bottom :12.5%
      .nav-wrap-text
        position :absolute
        top :0
        bottom :0
        left :0
        right :0
        text-align :center
        display :-webkit-box
        flex-direction :column
        justify-content :center
        .nav-icon
          padding-right :.1rem
</style>



